@tailwind base;
@tailwind components;
@tailwind utilities;


/* 1. Force the top banner iframe to be hidden */
iframe.goog-te-banner-frame {
    display: none !important;
}

/* 2. Prevent the body from being pushed down */
body {
    top: 0px !important;
    position: relative !important;
}

/* 3. Style the main translation bar */
#translation-bar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.25rem;
    background-color: #1F2937; /* gray-800 */
}
.dark #translation-bar-container {
    background-color: #000; /* black */
}

/* 4. Hide unwanted elements within the widget */
#google_translate_element_container .goog-te-gadget-simple {
    background-color: transparent !important;
    border: none !important;
}
#google_translate_element_container .goog-logo-link,
#google_translate_element_container .goog-te-gadget-simple > span {
    display: none !important;
}

/* 5. Style the main dropdown select element */
#google_translate_element_container select.goog-te-combo {
    cursor: pointer;
    background-color: #4B5563; /* Tailwind gray-600 */
    color: white !important;
    border: 1px solid #6B7280;
    border-radius: 4px;
    padding: 4px 8px;
    height: 28px;
    font-size: 0.875rem !important; /* text-sm */
    -webkit-appearance: none; /* Removes default browser dropdown arrow */
    -moz-appearance: none;
    appearance: none;
}
#google_translate_element_container select.goog-te-combo:focus {
    outline: none;
    box-shadow: 0 0 0 2px #3B82F6; /* blue-500 focus ring */
}


/* 6. Forcefully restyle the language list when it's active */
/* This is where the magic happens */
body.google-translate-active .goog-te-menu-frame {
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
    border-radius: 0.5rem;
    background-color: #111827; /* gray-900 */
    border: 1px solid #374151; /* gray-700 */
    max-height: 50vh; /* Make it scrollable on smaller screens */
    overflow-y: auto !important;
}
body.google-translate-active .goog-te-menu-frame .goog-te-menu2-col {
    /* Force a single column layout */
    float: none !important;
    width: 100% !important;
    padding: 0 !important;
}
body.google-translate-active .goog-te-menu-frame .goog-te-menu2-item,
body.google-translate-active .goog-te-menu-frame .goog-te-menu2-item-selected {
    color: #D1D5DB !important; /* gray-300 */
    padding: 0.5rem 1rem;
    display: block; /* Ensure full-width clickable area */
    width: 100%;
}
body.google-translate-active .goog-te-menu-frame .goog-te-menu2-item:hover {
    background-color: #374151 !important; /* gray-700 */
    color: #fff !important;
}
